// Fonts
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;700&display=swap&subset=latin-ext');
$default-font: 'Nunito', sans-serif;
//$default-font: 'Poppins', sans-serif
$caption-font: 'Nunito', sans-serif;

// Basic Monochrome Colors
$bsc-black: #000000;
$bsc-grey-dark: #343434;
$bsc-grey-medium: #b6b6b6;
$bsc-white: #ffffff;

// Text Colors
$txt-black: #262626;
$txt-dark: #303030;
$txt-medium: #616161;
$txt-light: #aaa;
$txt-muted: #d8d8d8;
$txt-white: #ffffff;
$txt-white-transparent: rgba(#ffffff, .6);

// Animations
$animation-delay: .3s;
$animation-duration: .8s;
