// Breakpoints
$S: 576px;
$M: 768px;
$L: 992px;
$XL: 1200px;
$XXL: 1600px;

$HS: 320px;
$HL: 560px;

// Media  queries
@mixin MQ($canvas) {
  @if $canvas == S {
    @media only screen and (min-width: $S) {
      @content; } }
  @else if $canvas == M {
    @media only screen and (min-width: $M) {
      @content; } }
  @else if $canvas == L {
    @media only screen and (min-width: $L) {
      @content; } }
  @else if $canvas == XL {
    @media only screen and (min-width: $XL) {
      @content; } }
  @else if $canvas == XXL {
    @media only screen and (min-width: $XXL) {
      @content; } } }

@mixin HEIGHT($screen) {
  @if $screen == HS {
    @media only screen and (min-height: $HS) {
      @content; } }
  @else if $screen == HL {
    @media only screen and (min-height: $HL) {
      @content; } } }

@mixin MAINBREAK($canvas, $screen) {
  @if ($canvas == XL and $screen == HL) {
    @media only all and (min-width: XL) and (min-height: $HL) {
      @content; } } }
